import React, { Component, Fragment } from "react";
import { TextField, Grid, CardContent, Button} from "@material-ui/core";
import { connect } from "react-redux";
import { siteIdentificationAction } from "../../actions/SurveyActions";
import Typography from "@material-ui/core/Typography";
import { GET, POST } from "../../services/restService";
import { siteSurvey } from "../../mockData/siteSurvey";

class FormSiteID extends Component {
    constructor(props) {
        super(props);
        this.state = {
            clients: [],
    }
    }
    componentDidMount(){ 
        const data = Object.assign(
                {},
                siteSurvey,
                this.props.siteId,
                this.props.saqSite,
                this.props.towerInfo,
                this.props.civilInfo,
                this.props.generatorInfo,
                this.props.gridPowerInfo,
                this.props.equipmentInfo,
                this.props.powerReading,
              );
              console.log(siteSurvey.SI_Site_ID)
        .then(response => {
            if (response && response.data) {
                this.setState({ clients: response.data });
                console.log(response.data)
            }
        })

        .catch(error => console.log(error));
    }
    render() {
        let { clients } = this.state;
        const { siteIdDispatch } = this.props;
        

        return (
            <Fragment>
                <Grid container spacing={4}>                    
                    <Grid item xs={12} lg={4}>
                        <div className="p-3">
                            {clients &&  clients.map(client => (
                            <div className="p-3">                            
                                <span className="font-weight-bold text-black">Site ID:</span>
                                <span className="text-center p-2">{client.SI_Site_ID}</span>                               
                            </div> 
                            ))}    
                            {clients &&  clients.map(client => (
                            <div className="p-3">
                                <span className="font-weight-bold text-black">Region:</span>
                                <span className="font-weight-normal  p-2">{client.SI_Region}</span>
                            </div>
                            ))}
                            {clients &&  clients.map(client => (
                            <div className="p-3">
                                <span className="font-weight-bold text-black">State:</span>
                                <span className="font-weight-normal p-2">{client.SI_State}</span>
                            </div>
                            ))}
                            {clients &&  clients.map(client => (
                            <div className="p-3">
                                <span className="font-weight-bold text-black">IF ESCO:</span>
                                <span className="font-weight-normal p-2">{client.SI_ESCO_Name}</span>
                            </div>
                            ))}
                            {clients &&  clients.map(client => (
                            <div className="p-3">
                                <span className="font-weight-bold text-black">Address:</span>
                                <span className="font-weight-normal p-2">{client.SI_Site_Addresses}</span>
                            </div>
                            ))}
                        </div>
                    </Grid>
                    <Grid item xs={12} lg={4}>
                        <div className="p-3">
                        {clients &&  clients.map(client => (
                            <div className="p-3">
                                <span className="font-weight-bold text-black">Latitude:</span>
                                <span className="text-center p-2">{client.SI_Latitude}</span>
                            </div>
                            ))}
                            {clients &&  clients.map(client => (
                            <div className="p-3">
                                <span className="font-weight-bold text-black">Longitude:</span>
                                <span className="font-weight-normal  p-2">{client.SI_Longitude}</span>
                            </div>
                            ))}
                            {clients &&  clients.map(client => (
                            <div className="p-3">
                                <span className="font-weight-bold text-black">Category:</span>
                                <span className="font-weight-normal p-2">{client.SI_Category}</span>
                            </div>
                            ))}
                            {clients &&  clients.map(client => (
                            <div className="p-3">
                                <span className="font-weight-bold text-black">On Air Date:</span>
                                <span className="font-weight-normal p-2">{client.SI_OnAir_Date}</span>
                            </div>
                            ))}
                        </div>
                    </Grid>
                    <Grid item xs={12} lg={4}>
                        <div className="p-3">
                           {clients &&  clients.map(client => (
                            <div className="p-3">
                                <span className="font-weight-bold text-black">No of Tenants:</span>
                                <span className="text-center p-2">{client.SI_No_of_Tenants}</span>
                            </div>
                            ))}
                            {clients &&  clients.map(client => (
                            <div className="p-3">
                                <span className="font-weight-bold text-black">Name of Tenants:</span>
                                <span className="text-center p-2">{client.SI_Name_of_Tenants}</span>
                            </div>
                            ))}
                            {clients &&  clients.map(client => (
                            <div className="p-3">
                                <span className="font-weight-bold text-black">Site Telemetry and Control:</span>
                                <span className="font-weight-normal  p-2">{client.SI_Site_Telemetry_Control}</span>
                            </div>
                            ))}
                            {clients &&  clients.map(client => (
                            <div className="p-3">
                                <span className="font-weight-bold text-black">Energy System:</span>
                                <span className="font-weight-normal p-2">{client.SI_Energy_System}</span>
                            </div>
                            ))}
                        </div>
                    </Grid>
                </Grid> 
            </Fragment>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        siteID: state.siteID,
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        siteIdDispatch: (data) => dispatch(siteIdentificationAction(data)),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(FormSiteID);