import React, { Component, Fragment } from "react";
import { TextField, Grid, FormControl, InputLabel, MenuItem, Select } from "@material-ui/core";
import { connect } from "react-redux";
import Button from "@material-ui/core/Button";
import { siteIdentificationAction } from "../actions/SurveyActions";
import { GET, POST } from "../services/restService";
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import {firstform} from "./formsurvey"
class FormSiteID extends Component {
  constructor(props) {
    super(props);
    this.state = {
      formData: {
        SI_Region: "",
        SI_State: "",
        SI_Site_ID: "",
        SI_Longitude: "",
        SI_Latitude: "",
        SI_OnAir_Date: "",
        SI_Site_Addresses: "",
        SI_Category: "",
        SI_No_of_Tenants: "",
        SI_Name_of_Tenants: "",
        SI_Site_Telemetry_Control: "",
        SI_Energy_System: "",
        SI_ESCO_Name: "",
        AuditID: "",
      }
    };
  }

  componentDidMount() {
    this.setState({ formData: this.props.siteID });
  }

  render() {
    const handleDateChange = (date) => {
      const formData = this.state.formData;
      formData.SI_OnAir_Date = date;
      this.setState({ formData: formData });
    }
    const { classes } = this.props;
    const { siteIdDispatch } = this.props;

    const onChangeValue = (event) => {
      const formData = this.state.formData;
      formData[event.target.id] = event.target.value;
      // TODO : form data validation
      this.setState({ formData: formData });
      siteIdDispatch(this.state.formData);
    }

    return (
      
      <Fragment>
        <TextField
                fullWidth
                className="m-2"
                label="Audit Id"
                id="AuditID"
                onChange={onChangeValue}
                value={this.state.formData.AuditID }
              />
        <Grid container spacing={4}>
          <Grid item xs={12} lg={4}>
          
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="Site ID"
                id="SI_Site_ID"
                onChange={onChangeValue}
                value={this.state.formData.SI_Site_ID }
              />
              {/* <TextField
                fullWidth
                className="m-2"
                label="Region"
                id="SI_Region"
                onChange={onChangeValue}
                value={this.state.formData.SI_Region }
              /> */}
              <FormControl fullWidth
              id="SI_Region"
              className="m-2"
              value={this.state.formData.SI_Region}
              onChange={onChangeValue}>
              <InputLabel>Region</InputLabel>
              <Select>
              <MenuItem value={'North'}>North</MenuItem>
              <MenuItem value={20}>East</MenuItem>
              <MenuItem value={30}>West</MenuItem>
              <MenuItem value={30}>South</MenuItem>
              </Select>
              </FormControl> 
              <TextField
                fullWidth
                className="m-2"
                label="State"
                id="SI_State"
                onChange={onChangeValue}
                value={this.state.formData.SI_State }
              />
              <TextField
                fullWidth
                className="m-2"
                label="Longitude"
                id="SI_Longitude"
                onChange={onChangeValue}
                value={this.state.formData.SI_Longitude }
              />
              <TextField
                fullWidth
                className="m-2"
                label="IF ESCO"
                id="SI_ESCO_Name"
                onChange={onChangeValue}
                value={this.state.formData.SI_ESCO_Name }
              />
            </div>
          </Grid>
          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="Latitude"
                id="SI_Latitude"
                onChange={onChangeValue}
                value={this.state.formData.SI_Latitude }
              />
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <Grid container justify="space-around">

      <KeyboardDatePicker
          fullWidth
          margin="normal"
          id="date-picker-dialog"
          label="Date picker dialog"
          format="dd/MM/yyyy"
          value={this.state.formData.SI_OnAir_Date}
          onChange={handleDateChange}
          KeyboardButtonProps={{
            'aria-label': 'change date',
          }}
        />
       
      </Grid>
    </MuiPickersUtilsProvider>
              <TextField
                fullWidth
                className="m-2"
                label="Address"
                id="SI_Site_Addresses"
                onChange={onChangeValue}
                value={this.state.formData.SI_Site_Addresses }
              />
              <TextField
                fullWidth
                className="m-2"
                label="Category"
                id="SI_Category"
                onChange={onChangeValue}
                value={this.state.formData.SI_Category }
              />
            </div>
          </Grid>
          <Grid item xs={12} lg={4}>
            <div className="p-3">
              <TextField
                fullWidth
                className="m-2"
                label="No of Tenants"
                id="SI_No_of_Tenants"
                onChange={onChangeValue}
                value={this.state.formData.SI_No_of_Tenants }
              />
              <TextField
                fullWidth
                className="m-2"
                label="Name of Tenants"
                id="SI_Name_of_Tenants"
                onChange={onChangeValue}
                value={this.state.formData.SI_Name_of_Tenants }
              />
              <TextField
                fullWidth
                className="m-2"
                label="Site Telemetry and Control"
                id="SI_Site_Telemetry_Control"
                onChange={onChangeValue}
                value={this.state.formData.SI_Site_Telemetry_Control }
              />
              <TextField
                fullWidth
                className="m-2"
                label="Energy System"
                id="SI_Energy_System"
                onChange={onChangeValue}
                value={this.state.formData.SI_Energy_System }
              />
              
            </div>
          </Grid> 
        </Grid>
      </Fragment>

    );
  }
}

const mapStateToProps = (state) => {
  return {
    siteID: state.siteID,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    siteIdDispatch: (data) => dispatch(siteIdentificationAction(data)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(FormSiteID);