const mapStateToProps = (state) => {
  console.log(
    state.siteID,
    state.saqSite,
    state.towerInfo,
    state.civilInfo,
    state.generatorInfo,
    state.gridPowerInfo,
    state.equipmentInfo,
    state.powerReading
  );

  return {
    siteId: state.siteID,
    saqSite: state.saqSite,
    towerInfo: state.towerInfo,
    civilInfo: state.civilInfo,
    generatorInfo: state.generatorInfo,
    gridPowerInfo: state.gridPowerInfo,
    equipmentInfo: state.equipmentInfo,
    powerReading: state.powerReading,
  };
};

